			<div class="groupexhibitiondetail-footer flex flex-row flex-nowrap align-end"
				:class="activeIndex > 0 ? 'justify-end' : 'justify-center'">
				<div class="ged-list " style="overflow:auto;">
					<ul v-if="activeIndex == 0" class="flex flex-row flex-nowrap align-center justify-center"
						style="margin: auto">
						<li class="ged-list-title"></li>
						<li :class="activeIndex == index ? 'active' : ''" v-for="(item, index) in secondlist"
							@click="fullClick(index)" :key="index" style="margin-top: 0px !important;"
							v-if="index!= indexLength-1">
							<!-- <img :src="item.img"> -->
							<div class="align-endimgs" :style="{
                  background: 'url(' + item.img + ') center no-repeat',
                }"></div>
							<span>
