					<!-- justify-center -->
					<!-- showlist -->
					<!-- detail -->
					<ul v-if="activeIndex != 0&&activeIndex!= indexLength-1" :class="inAn?'showFY':''"  @animationend='inAn=false'   style="overflow:auto;display:flex;max-width: 65% !important;width: 65%;float: right;">

						<li class="ged-list-title"></li>
						<li :class="detailIndex == index ? 'active itemList' : 'itemList '"
							v-for="(item, index) in secondlist[secondIndex].smalllist" :key="index"
							@click="detailClick(index)">
