						<VueSlickCarousel class="vidtion-conn" v-bind="setting">

							<li :class="detailIndex == index ? 'active itemList' : 'itemList'"
								v-for="(item, index) in listzw" @click="detailClick(item, index)" :key="index">
								<div class="align-endimgs" :style="{
                  background: 'url(' + item.images + ') center no-repeat',
                }"></div>
								<span>&nbsp;</span>
							</li>
