            fadeInRight
            animated
          " v-if="activeIndex > 0">
					<div class="ged-page-number flex flex-row align-center">
						<span>{{
              activeIndex < 9 ? `0${activeIndex + 1}` : activeIndex + 1
            }}</span>/{{
              secondlist.length < 10
