		animation: 8s wordsLoop linear infinite normal;
	}

	@keyframes wordsLoop {
		0% {

			transform: translateY(-10px);
			-webkit-transform: translateY(-20px);
		}

		100% {
			transform: translateY(95px);
			-webkit-transform: translateY(95px);
		}
	}

	@-webkit-keyframes wordsLoop {
		0% {

			transform: translateY(-10px);
			-webkit-transform: translateY(-20px);
		}

		100% {
			transform: translateY(95px);
			-webkit-transform: translateY(95px);
		}
	}

	.switch {
		display: block;
		width: 25px;
		height: 25px;
		font-size: 20px;
		position: absolute;
		background: center url(../assets/img/audio.png);
		background-size: contain;
		/*  */
		background-repeat: no-repeat;
		margin-left: 12px;
		margin-top: 5px;
		animation: mymove 1s linear infinite;
		-webkit-animation: mymove 1s linear infinite;
	}

	@keyframes mymove {
		0% {
			transform: scale(0.9);
		}

		100% {
			transform: scale(1);
		}
	}


	.switch:hover {
		color: blue;
