		/*  */
		background-repeat: no-repeat;
		margin-left: 12px;
		margin-top: 5px;
	}

	.expand:hover {
		color: blue;
	}

	.progress {
		width: 430px;
		height: 10px;
		border-radius: 3px;
		overflow: hidden;
		background-color: #555;
		cursor: pointer;
		position: absolute;
		top: 16px;
		left: 45px;
	}

	.loaded {
		width: 0;
		height: 100%;
