	.vidtion-conn {
		display: flex;
		/*  flex-wrap: nowrap;
      justify-content: flex-end; */
		width: calc(100% - 180px);
		/* 		-webkit-animation: fadeInUp11 1s .2s ease both;
		-moz-animation: fadeInUp11 1s .2s ease both; */

	}

	/* 	#animation {
		-webkit-animation: fadeInUp 1s .2s ease both;
		-moz-animation: fadeInUp 1s .2s ease both;
	} */
	.showFY {
		-webkit-animation: fadeInUp11 1s .2s ease both;
		-moz-animation: fadeInUp11 1s .2s ease both;
		animation: fadeInUp11 1s;
	}

	@-webkit-keyframes fadeInUp11 {
		0% {
			opacity: 0;
		}

		100% {
			opacity: 1;
		}
	}

	@-moz-keyframes fadeInUp11 {
		0% {
			opacity: 0;
			-moz-transform: translateY(20px)
		}

		100% {
			opacity: 1;
			-moz-transform: translateY(0)
		}
	}

	.showContent {
		width: 100%;
		height: 100%;
		position: fixed;
		top: 0px;
		left: 0;
		display: flex;
		align-items: center;
		justify-content: center;
